import { request } from "./request";




//
export function addCart(data){
   return request({
        url:'/api/carts',
        method:'POST',
        data
    })
}


// data={num:1}
export function modifyCart(id,data){
    return request({
         url:`/api/carts/${id}`,
         method:'put',
         data
     })
 }

//  checked,id
export function checkedCart(data){
    return request({
         url:'/api/carts/checked',
         method:'patch',//update
         data
     })
 }

//
export function getCart(){
    return request({
        url:`/api/carts`,

     })
 }

//
export function deleteCartItem(id){
    return request({
         url:`/api/carts/${id}`,
         method:'delete'
     })
 }



