import { request } from "./request";

//
export function addAcart(params){
    return request({
        url:'/api/addAcart',
        method:'post',
        params 
    })
}
//
export function getCartList(){
    return request({
        url:'/api/getCartList',
    })
}
//ID
export function getEditCart(id){
    return request({
        url:`/api/editCart/${id}`,

    })
}
//
export function editCart(id,params){
    return request({
        url:`/api/editCart/${id}`,
        method:'put',
        params
    })
}
//
export function deleteCart(id){
    return request({
        url:`/api/deleteCart/${id}`,
        method:'delete',
    })
}




